import THREE from 'three';
import deadTree from '../deadtree';
import _Lighting from './Lighting';

let Stage = (Scene) => {

	Scene.objects = [];
	Scene.namedObjects = {};

	let groundPlane = new THREE.Mesh(
		new THREE.PlaneGeometry(100000, 100000),
		new THREE.MeshLambertMaterial({
			color: '#888'
		})
	);
	groundPlane.receiveShadow = true;
	groundPlane.position.y = -2;
	groundPlane.rotation.x -= Math.PI / 2;
	Scene.add(groundPlane);
	Scene.namedObjects.groundPlane = groundPlane;

	let gridPlane = new THREE.Mesh(
		new THREE.PlaneGeometry(100, 100),
		new THREE.MeshLambertMaterial({
			color: '#ccc'
		})
	);
	gridPlane.receiveShadow = true;
	gridPlane.rotation.x -= Math.PI / 2;
	Scene.add(gridPlane);
    Scene.objects.push(gridPlane);
	Scene.namedObjects.gridPlane = gridPlane;

	let Lighting = _Lighting(Scene);
	Lighting.setTime(0.333);

	Scene.setTime = Lighting.setTime;
	Scene.getTime = Lighting.getTime;

	/* var tree = deadTree(5, new THREE.MeshPhongMaterial({ color: 0x555555 }), 5);
	console.log('three tree is', tree);
	Scene.add(tree);
*/
	return Scene;

};

export default Stage;
